/* Highlight hovered element */
.wcf-animb--hover-highlight {
  outline: 2px solid red;
}

/* Popup styles */
.wcfanimb-popup {
  position: fixed;
  background: #1f2124;
  border-radius: 6px;
  font-family: Arial, sans-serif;
  box-shadow: 0px 2px 24px 0px rgba(0, 0, 0, 0.15);
  z-index: 999999999;
  width: 328px;

  .wcfanimb-wrapper {
    position: relative;
    padding: 16px;
    display: flex !important;
    justify-content: center;
    align-items: start;
    flex-direction: column;
    gap: 16px;
    color: #fff;

    .wcfanimb-close-btn {
      position: absolute;
      top: 10px;
      right: 10px;
    }

    .wcfanimb-label {
      margin: 0;
      padding: 0;
      color: white;
      margin-bottom: 6px;
      font-size: 14px;
    }

    .wcfanimb-classes {
      padding: 10px 8px;
      background-color: #2b2c31;
      border-radius: 6px;

      .wcfanimb-popupContent {
        margin: 0;
        padding: 0;
        color: white;
        font-size: 13px;
        width: 100%;
        overflow: hidden;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 20;
        &.close{
          -webkit-line-clamp: 1;
        }
      }
    }

    .wcfanim-btn-group {
      width: 100%;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      gap: 6px;

      .wcfanimb-copy-btn {
        padding: 6px 12px;
        font-size: 14px;
        line-height: 1.14;
        color: white;
        background-color: #3f444b;
        border: 0;
        border-radius: 6px;
        cursor: pointer;

        &:hover {
          background-color: #3f444b;
          color: white;
        }
      }
      .wcfanimb-select-btn {
        padding: 6px 12px;
        font-size: 14px;
        line-height: 1.14;
        color: white;
        background-color: #fc6848;
        border: 0;
        border-radius: 6px;
        cursor: pointer;

        &:hover {
          background-color: #fc6848;
          color: white;
        }
      }
    }
  }
}
